.list {
    list-style: none;
    display: flex;
    gap: 4rem;
    justify-content: center;
    align-items: center;
}

.link {
    text-decoration: none;
    font-size: 20px;
    color: rgb(150, 66, 10);
    font-weight: 700;
    transition: all .4s;
}

.link:hover {
    opacity: 0.7;
}